.home-wrap {
	background-color: #fff;
}

.home-banner {
	padding: 80px 0;
	background: linear-gradient(rgba(0,0,0,0.3), rgba(0,0,0,0.6)), url(/img/bg_login.jpg);
	background-position: center;

	.kksearch {
		.search-area{
			position: relative;

			.clear-input{
				right:80px;
				line-height:48px;
			}
		}

		h2{
			color:#fff;
			text-align:left;
			margin:0 0 15px 0;
			font-weight: bolder;
			font-size: 2.4rem;
			text-shadow: 0 0 5px rgba(0,0,0,0.2);
		}

		.input-group {
			box-shadow: 0 0 10px rgba(0,0,0,0.2);

			.form-control{
				height:48px;
				border: 0;
			}

			.input-group-btn .btn{
				height: 48px;
				font-size: 1.6rem;

				i{
					top:0;
				}
			}
		}

		.search-dropdown{
			top:48px;
			border-top: 1px solid #eee;
			border-radius: 0 0 6px 6px;
			box-shadow:0 5px 10px rgba(0,0,0,.1);
		}
	}
}

.home-section {
	padding: 20px 0 5px 0;
	border-top: 1px solid #eee;

	.section-title {
		margin-top: 0;
		margin-bottom: 10px;
	}

	.btn {
		margin: 5px 5px 10px 0;
	}
}
